import { useEffect, useState, createContext } from "react";

//       
const AppContext = createContext({
  user: "",
  accessToken: "",
  role: "",
  cafeId: "", //  cafeId 
  setUser: function (user) {
    this.user = user;
  },
  setAccessToken: function (accessToken) {
    this.accessToken = accessToken;
  },
  setRole: function (role) {
    this.role = role;
  },
  setCafeId: function (cafeId) { //  cafeId setter
    this.cafeId = cafeId;
  },
});

export default AppContext;

// AppContext  provider    
//    localStorage   (context!!) -    
export function AppContextProvider(props) {
  const [user, setUser] = useState(localStorage.getItem("user") || "");
  const [accessToken, setAccessToken] = useState(
      localStorage.getItem("accessToken") || ""
  );
  const [role, setRole] = useState(localStorage.getItem("role") || "");
  const [cafeId, setCafeId] = useState(localStorage.getItem("cafeId") || ""); //  cafeId 

  const value = {
    user,
    accessToken,
    role,
    cafeId, // context cafeId 
    setUser,
    setAccessToken,
    setRole,
    setCafeId, // cafeId setter 
  };

  //   localStorage  
  useEffect(() => {
    localStorage.setItem("user", user);
    localStorage.setItem("accessToken", accessToken);
    localStorage.setItem("role", role);
    localStorage.setItem("cafeId", cafeId); // cafeId localStorage 
  }, [user, accessToken, role, cafeId]);

  return (
      <AppContext.Provider value={value}>
        {props.children} {/**  value    */}
      </AppContext.Provider>
  );
}
