import { useEffect, useState } from "react";
import "./CafeType.css";

const CafeType = ({ cafeType, setCafeType }) => {

  const handleTypeRadioChange = (event) => {
    setCafeType(event.target.value);
  };
 
  return (
    <div className="cafe-register-box">
      <div className="cafe-register-title">
        <p>  <br /></p>
        <hr />
      </div>
      <div className="cafe-register-content">
        <div className="type-radio">
          <div>
            <input
                type="radio"
                id="typeIndividual"
                name="cafeType"
                value="G"
                checked={cafeType === "G"}
                onChange={handleTypeRadioChange} /> 
            <label htmlFor="typeIndividual"></label>
          </div>
          <div>
            <input
              type="radio"
              id="typeFranchise"
              name="cafeType"
              value="P"
              checked={cafeType === "P"}
              onChange={handleTypeRadioChange} /> 
            <label htmlFor="typeFranchise"></label>
          </div>
        </div>
      </div>
    </div>
  );
};

export default CafeType;
