import React from "react";
import "./FinishModal.css";

const ConfirmModal = ({ isOpen, onClose, onConfirm }) => {
  if (!isOpen) return null;

  return (
    <div className="finish-modal">
      <div className="finish-modal-content">
        <p className="finish-title">  ?</p>
        <div className="finish-modal-buttons">
          <button className="finish-modal-close-button" onClick={onClose}></button>
          <button className="finish-modal-confirm-button" onClick={onConfirm}></button>
        </div>
      </div>
    </div>
  );
};

export default ConfirmModal;