import React, { useState } from "react";
import "./CancelModal.css";
import Swal from "sweetalert2";

const CancelModal = ({ isOpen, onClose, onConfirm, reservationIds }) => { // 'reservationIds' 

  const [cancelReasonId, setCancelReason] = useState("");

  const handleReasonChange = (event) => {
    setCancelReason(event.target.value);
  };

  const handleConfirm = () => {
    if (cancelReasonId) {
      onConfirm(reservationIds, cancelReasonId); // 'reservationIds' 
    } else {
      Swal.fire({
        icon: "warning",
        title: "",
        text: "  .",
        confirmButton: true,
        confirmButtonText: "",
        confirmButtonColor: "#FFCD4A",
        customClass: {
          confirmButton: 'no-outline',
        }
      });
    }
  };

  if (!isOpen) return null;

  return (
      <div className="cancel-modal">
        <div className="cancel-modal-content">
          <p className="cancel-title"> ?</p>
          <p className="cancel-inform">     .<br />  .</p>
          <select className="cancel-reason" defaultValue="" onChange={handleReasonChange}>
            <option value="" disabled> </option>
            <option value="F"></option>
            <option value="N"> </option>
            <option value="I">  </option>
            <option value="B">   </option>
            <option value="E"></option>
          </select>
          <div className="cancel-modal-buttons">
            <button className="cancel-modal-close-button" onClick={onClose}></button>
            <button className="cancel-modal-confirm-button" onClick={handleConfirm}> </button>
          </div>
        </div>
      </div>
  );
};

export default CancelModal;
