import React, { useCallback, useContext, useState } from "react";
import "./Login.css";
import Header from "./Header";
import { Link, useNavigate } from "react-router-dom";
import ManagerNav from "./Manager/ManagerNav";
import AppContext from "./AppContext";
import { addAuthHeader, removeAuthHeader } from "./apis/axiosConfig";
import { login } from "./apis/login";
import Swal from "sweetalert2";

function Login() {
  const navigate = useNavigate();

  const register = (event) => {
    navigate("/register");
  };

  const [user, setUser] = useState({
    userName: "",
    password: "",
  });

  const appContext = useContext(AppContext);

  const handleChange = useCallback((event) => {
    setUser((prevUser) => {
      return { ...prevUser, [event.target.name]: event.target.value };
    });
  }, []);

  const handleLogin = useCallback(
    async (event) => {
      try {
        if (user.userName === "" && user.password === "") {
          Swal.fire({
            icon: "warning",
            title: "",
            text: "    .",

            confirmButton: true,
            confirmButtonText: "",
            confirmButtonColor: "#FFCD4A",
            customClass: {
              confirmButton: "no-outline",
            },
          });
        } else if (user.userName === "") {
          Swal.fire({
            icon: "warning",
            title: "",
            text: "   .",

            confirmButton: true,
            confirmButtonText: "",
            confirmButtonColor: "#FFCD4A",
            customClass: {
              confirmButton: 'no-outline',
            }
        })
        }
        else if(user.password === ""){
          
            Swal.fire({
              icon: "warning",
              title: "",
              text: `   .`,
              
              confirmButton: true,
              confirmButtonText: "",
              confirmButtonColor: "#FFCD4A",
              customClass: {
                confirmButton: 'no-outline',
              }
          })
        }
        else{
          // 
          const response = await login(user);

          //   Authorization 
          addAuthHeader(response.data.data.accessToken);

          //Context   
          appContext.setUser(response.data.data.userName);
          appContext.setAccessToken(response.data.data.accessToken);
          appContext.setRole(response.data.data.role);

          // 
          setUser({
            userName: "",
            password: "",
          });

          if (response.data.data.role === "ROLE_MANAGER") {
            navigate("/manager");
          } else {
            navigate("/user");
          }
        }
      } catch (error) {
        console.log(error.response.data.isSuccess);
        if(error.response.data.code === 3002){
          Swal.fire({
            icon: "warning",
            title: "",
            text: error.response.data.message,
            
            confirmButton: true,
            confirmButtonText: "",
            confirmButtonColor: "#FFCD4A",
            customClass: {
              confirmButton: 'no-outline',
            }
        })
          
        }
      }
    },
    [appContext, user]
  );

  const handleLogout = (event) => {
    //   Authorization 
    removeAuthHeader();

    //Context   
    appContext.setUser("");
    appContext.setAccessToken("");
    appContext.setRole("");
  };

  return (
    <div className="login">
      <Header />
      <div className="login-container">
        <h1 className="login-title">LOGIN</h1>
        <div className="login-box">
          <h1 className="login-intro">   .</h1>
          {appContext.user === "" ? (
            <div className="login-before">
              <div className="login-inputs">
                <div className="id-box">
                  <label className="id"></label>
                  <input
                    type="text"
                    placeholder=""
                    id="userName"
                    name="userName"
                    value={user.userName}
                    onChange={handleChange}
                  />
                </div>
                <div className="pw-box">
                  <label className="pw"></label>
                  <input
                    type="password"
                    placeholder=""
                    id="password"
                    name="password"
                    value={user.password}
                    onChange={handleChange}
                  />
                </div>
              </div>
              <div className="login-button">
                <button onClick={handleLogin}></button>
              </div>
              <div className="go-register-section">
                <label className="notyet-register">
                     ?
                </label>
                <Link to={"/register"} className="go-register">
                  
                </Link>
              </div>
            </div>
          ) : (
            <div className="loginafterbox-loginpage">
              <button className="logoutButton" onClick={handleLogout}>
                
              </button>
            </div>
          )}
        </div>
      </div>
    </div>
  );
}

export default Login;
